*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family:"montseratt",sans-serif;
  background-color: rgb(141, 160, 144);
}
.app{
  width: 300px;
  height: 500px;
  margin: 0 auto;
  padding: auto;
  background-image: url("./assests/cold.jpg");
  background-size: cover;
  background-position: bottom;
  transition: 1.4s ease-out;
}

@media screen and (min-width: 900px) {
  .app {
    width: 50%;
  height: 500px;
  }
}
.app.warm{
  background-image:url("./assests/hot.jpg") ;
}
main{
  min-height: 500px;
  padding: 25px;
  background-image: linear-gradient(to top , rgba(0,0,0,0.2), rgba(0,0,0,0.8));
  z-index: -1;
}


.search-bar{
  width: 100%;
  margin: 0 0 40px;
  border: none;
}
.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;
  /* border: none; */
  background-color: rgba(255,255,255,0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;
  box-shadow: 0px 5px rgba(0,0,0,0.2);
  color:#313131;
  font-size: 20px;
  transition: 0.4s ease;

}

.search-box .search-bar:focus{
  background-color: rgba(255,255,255,0.75);
  border: none;
}

.location-box .location{
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}
.location-box .date{
  color: #fff;
  font-size: 20px;
  font-weight: 300px;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50,50,70,0.5);
}
.weather-box{
  text-align: center;
}

 .temp{
  /* position: relative; */
  display: inline-block;
  margin: 5px 5px;
  background-color: rgba(255,255,255,0.2);
  border-radius: 16px;
  padding: 15px 25px;
  color: #fff;
  font-size: 15px;
  font-weight: 400;
  text-shadow: 3px 6px (50,50,70,0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,0.2);

}
.degree{
width: 25px;
height: 25px;
background-color:transparent;
cursor: pointer;
border-radius: 5px;
margin-left:10px;
font-size: small;
}
.f-to-c:hover{
  color: #fff;
  background-color: rgb(121, 121, 121);
  /* font-size:large; */
}

.weather-box .weather{
  color: #fff;
  font-size: 30px;
  font-weight: 500;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}

.errorBox{
  width: 100%;
  height: 20px;
  background-color: aqua;
  /* display: none; */
  
}