.popup{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height:500px;
    background-color: rgba(0,0,0,0.2);
    display: flex;
    justify-content: center;
    align-items: center;
    transition:4s ease-in-out;;
}
.popup-inner {
    position: relative;
    padding: 20px;
    width: 40%;
    border-radius: 5px;
    background-color: rgb(230, 216, 216);

}
.popup-inner .close-btn{
    position: absolute;
    top: 16px;
    right: 16px;
    background-color: rgb(235, 51, 51);
    cursor: pointer;
    border-radius: 5px;
    padding: 3px;
    
}
.close-btn:hover{
    color: white;
}